li:hover {
    width: 100%;
}
.TodoItem {
    margin: 0 auto;
    background-color: white;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    border-radius: 10px;
    width: 60%;
    box-shadow: 0px 0px 20px 3px rgba(0,0,0,0.75);
}
.TodoItem-p {
    margin: 24px 0 24px 24px; 
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
    color: black;
}
.TodoItem-p--complete {
    text-decoration: line-through;
}
.TodoItem img {
    height: 32px;
    width: 32px;
}
