.icon-container {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}
.icon-container-check {
    position: absolute;
    left: 12px;
}
.icon-container-check--active {
    color: #4caf50;
}
.icon-container-delete {
    position: absolute;
    right: 0;
}
.icon-container-delete:hover {
    color: red;
}
.icon-svg {
    width: 32px;
    height: 32px;
}

.icon-container-check:hover .icon-svg{
    fill: #4caf50;
}

.icon-container-delete:hover .icon-svg{
    fill: red;
}
